import Demo from './NewAppointment';
import CalendarTodayIcon from '@mui/icons-material/CalendarToday';

import SlotTime from './SlotTime';
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  Container,
  Grid,
  Card,
  CardContent,
  Typography,
  Button,
  Dialog,
  DialogTitle,
  DialogActions,
  TextField,
  Select,
  MenuItem,
  Avatar,
  RadioGroup,
  DialogContent,
  Box
} from '@mui/material';
import { useMediaQuery, useTheme } from '@mui/material';
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';
import ResponsiveAppBar from './Navbar';
import BounceLoader from 'react-spinners/BounceLoader';
import '../Components/Styles/Event.css'
 import useAxios from '../axios';
 import { useNavigate } from 'react-router-dom';

function EventViewer() {
  const [selectedDate, setSelectedDate] = useState(null);
  const [showDialog, setShowDialog] = useState(false);
  const [appointments, setAppointments] = useState([]);
  const [allAppointments, setAllAppointments] = useState([]);
  const [empId, setEmpId] = useState('');
  const [logs, setLogs] = useState(null);
  const [empApps, setEmpApps] = useState([]);
  const [initialFetchCompleted, setInitialFetchCompleted] = useState(false);
  const [loading, setLoading] = useState(true);
  const [open, setOpen] = useState(false);
  const [selectedAppointment, setSelectedAppointment] = useState(null);

  const authorizedIds = ['00055333', '00002712', '00055334','00001221'];
  const isAuthorized = authorizedIds.includes(empId);

  const isMD_PA = empId === '00001221';

 
const navigate = useNavigate();

  useEffect(() => {
    const datalocal = JSON.parse(localStorage.getItem("user"));
    fetchAllAppointmentsData().then(() => {
      setInitialFetchCompleted(true);
      if (datalocal !== null) {
        setEmpId(datalocal?.emp_id || '');
      }
    });
    setTimeout(() => {
      setLoading(false);
    }, 2000);
  }, []);

  
  useEffect(() => {
    if (initialFetchCompleted && empId) {
      getLogDetail(empId);
      if (isAuthorized) {
        fetchAppointmentsData();
      } else {
        fetchEmpAppointmentsData();
      }
    }
  }, [initialFetchCompleted, empId, isAuthorized]);

  const handleClose = () => {
    setOpen(false);
    
   
  };

  const getLogDetail = async (empId) => {
    try {
      const response = await axiosData.get(`user/${empId}`);
      const logdata = response.data;
      if (logdata) {
        setLogs(logdata);
      } else {
        console.error('No user details found for this empId');
      }
    } catch (error) {
      console.error('Error fetching user details:', error);
    }
  };



  const axiosData = useAxios();

  const fetchAllAppointmentsData = async () => {
    try {
      const response = await axiosData.get('appointments');
      const alldatas = response.data.filter(appointment => appointment.Status !== 'S','B');
      setAllAppointments(alldatas);
    } catch (error) {
      console.error('Error fetching all appointments data:', error);
    }
  };


  
  const fetchAppointmentsData = async () => {
    try {
      const meetPerson = allAppointments[0].Meet_person;
      const response = await axiosData.get(`md-appoinments/${empId}`);
      const data = response.data.filter(appointment => appointment.Status !== 'S');
      setAppointments(data);
    } catch (error) {
      console.error('Error fetching appointments data:', error);
    }
  };

  const fetchEmpAppointmentsData = async () => {
    try {
      const response = await axiosData.get(`emp-appointments/${empId}`);
      const datas = response.data.filter(appointment => appointment.Status !== 'S');
      setEmpApps(datas);
    } catch (error) {
      console.error('Error fetching employee appointments data:', error);
    }
  };

  const handleDateClick = (date) => {
    setSelectedDate(date);
    setShowDialog(true);
  };

  const handleCloseDialog = () => {
    setShowDialog(false);
  };

  const dataAxios = useAxios();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const handleAccept = async (slot_id) => {
    try {
      const responseNewAppointments = await dataAxios.put(`appointments/new/${slot_id}`, { 
        status: 'A', 
        Approved_by: logs.C_EmployeeName 
      });
  
      if (responseNewAppointments.status === 200) {
        setAppointments((prevAppointments) =>
          prevAppointments.map((appointment) =>
            appointment.slot_id === slot_id ? { ...appointment, status: 'A' } : appointment
          )
        );
     // Reload the page if necessary
      }
    } catch (error) {
      console.error('Error updating appointment status:', error);
    }
    fetchAllAppointmentsData()
    setOpen(false);
  };
  
  const handleReject = async (slot_id) => {
    try {
      const response = await dataAxios.put(`appointments/new/${slot_id}`, { 
        status: 'R', 
        Approved_by: logs.C_EmployeeName 
      });
  
      if (response.status === 200) {
        setAppointments((prevAppointments) =>
          prevAppointments.map((appointment) =>
            appointment.slot_id === slot_id ? { ...appointment, status: 'R' } : appointment
          )
        );
        // Reload the page if necessary
      }
    } catch (error) {
      console.error('Error updating appointment status:', error);
    }
    setOpen(false);
    fetchAllAppointmentsData()
  };
  

  const handleCardClick = (appointment) => {
    setSelectedAppointment(appointment);
    setOpen(true);
  };

  const filteredEmpApps = selectedDate
    ? empApps.filter(appointment => {
      const appointmentDate = new Date(appointment.slot_date);
      return selectedDate.toDateString() === appointmentDate.toDateString();
    })
    : [];

  const filteredAppointments = selectedDate
    ? appointments.filter(appointment => {
      const appointmentDate = new Date(appointment.slot_date);
      return selectedDate.toDateString() === appointmentDate.toDateString();
    })
    : [];

  const combinedAppointments = [...filteredEmpApps, ...filteredAppointments];
  const formattedDate = selectedAppointment ? new Date(selectedAppointment.slot_date).toISOString().split('T')[0] : '';
  return (
    <>
      <ResponsiveAppBar />
      <div>
        {loading ? (
          <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
            <BounceLoader color={"maroon"} loading={loading} size={60} />
          </div>
        ) : (
          <Container sx={{ marginTop: "90px" }} maxWidth="lg">

            <div sx={{ marginTop: "90px" }}>
              <div className='calcevContainer'>
                <div className='calcev'>
                  <Calendar className="calan" onClickDay={handleDateClick} />
                  <div className='newbtnss'><Demo /></div>
                  {isMD_PA && (
                    <div className='newbtnss' style={{ right: "75%" }}><SlotTime /></div>)
                  }
                </div>
              </div>
              {showDialog && selectedDate && (
                <div className="modal">
                  <div className="modal-content">
                    <span className="close" onClick={handleCloseDialog}>&times;</span>
                    <h2 style={{ margin: "20px" }}>Appointments on {selectedDate.toDateString()}</h2>
                    {combinedAppointments.length > 0 ? (
                      <Container>
                        <Grid container spacing={2}>
                          {!isAuthorized ?
                            empApps.map((appointment, index) => (
                              <Grid item xs={12} sm={6} md={4} lg={3} key={index}>
                                <Card className='cardsdesign' onClick={() => handleCardClick(appointment)}>
                                  <CardContent>
                                    <div style={{ display: 'flex', alignItems: 'center', padding: "10px" }}>
                                      <Avatar style={{ marginRight: '16px' }}/>
                                      <Typography style={{ fontSize: '1.2rem', fontWeight: 500, marginBottom: "15px" }} variant="h5" component="div">
                                        {appointment.Employee_name} 
                                        </Typography>
                                        <Typography style={{ fontSize: '1rem', fontWeight: 500, marginBottom: "10px"  }} variant="h7" component="div">
                                        {appointment.Employee_Department} 
                                        </Typography>

                                    </div>
                                    <Typography style={{ marginTop: "10px" }} variant="h5" component="div">
                                      {appointment.Discussion_topic}
                                    </Typography>
                                    {isMD_PA ? (
  <Typography style={{ marginTop: "10px" }} variant="body2">
    {appointment.Meet_person}
  </Typography>
) : (
  <Typography style={{ marginTop: "10px" }} variant="body2">
    {appointment.Description}
  </Typography>
)}
                                    <Typography style={{ marginTop: "10px" }} variant="body2">
                                      {appointment.slot_from}-{appointment.slot_to}
                                    </Typography>
                                    {(!isAuthorized || appointment.Status === 'B' || appointment.Status === 'A' || appointment.Status === 'R') && (
                                      <Typography
                                        variant="button"
                                        display="block"
                                        gutterBottom
                                        sx={{
                                          backgroundColor: appointment.Status === 'A' ? 'green' : appointment.Status === 'R' ? 'red' : 'yellow',
                                          color: appointment.Status === 'A' || appointment.Status === 'R' ? 'white' : 'black',
                                          padding: '6px 16px',
                                          borderRadius: '4px',
                                          textAlign: 'center',
                                          display: 'inline-block',
                                          marginTop: '20px'
                                        }}
                                      >
                                        {appointment.Status === 'A' ? 'Accepted' : appointment.Status === 'B' ? 'Booked' : appointment.Status === 'R' ? 'Rejected' : ''}
                                      </Typography>
                                    )}
                                    {isAuthorized && appointment.Status !== 'A' && appointment.Status !== 'R' && (
                                      <>
                                        <div style={{ display: 'flex', marginTop: "10px", marginLeft: "5%" }}>
                                          <Button
                                            variant="outlined"
                                            onClick={() => handleAccept(appointment.slot_id, appointment.slot_from, appointment.Meet_person)}
                                            sx={{ backgroundColor: "green", color: "white" }}
                                            size="small"
                                          >
                                            Accept
                                          </Button>&nbsp;
                                          <Button
                                            variant="outlined"
                                            onClick={() => handleReject(appointment.slot_id)}
                                            className='rejbtn'
                                            sx={{ backgroundColor: "red", marginLeft: "10%", color: "white" }}
                                            size="small"
                                          >
                                            Reject
                                          </Button>
                                        </div>
                                      </>
                                    )}
                                  </CardContent>
                                </Card>
                              </Grid>
                            )) :
                            filteredAppointments.map((appointment, index) => (
                              <Grid item xs={12} sm={6} md={4} lg={3} key={index}>
                                <Card className='cardsdesign' sx={{cursor:'pointer'}} onClick={() => handleCardClick(appointment)}>
                                  <CardContent>
                                    <div style={{ display: 'flex', alignItems: 'center', padding: "10px" }}>
                                      <Avatar style={{ marginRight: '16px', marginTop:'10px' }} />
                                      <Typography style={{ fontSize: '1.2rem', fontWeight: 500, marginBottom: "3px",marginTop:"-10px" }} variant="h5" component="div">
                                        {appointment.Employee_name}
                                      </Typography>
                                      
                                    </div>
                                    <Typography style={{ fontSize: '0.8rem', fontWeight: 500,marginLeft: isMobile ?'-10px' : '45px',marginTop:'-15px' }} variant="h7" component="div">
                                       ( {appointment.Employee_Department} )
                                        </Typography>
                                    <Typography style={{ marginTop: "40px" }} variant="h5" component="div">
                                      {appointment.Discussion_topic}
                                    </Typography>
                                    {!isMD_PA ? (
                                      <Typography style={{ marginTop: "10px" }} variant="body2">
                                      {appointment.Description}
                                    </Typography>
 
) : (<Typography style={{ marginTop: "10px" }} variant="body2">
  To Meet: {appointment.Meet_person}
</Typography>
  
)}
 
                                    <Typography style={{ marginTop: "10px" }} variant="body2">
                                      {appointment.slot_from}-{appointment.slot_to}
                                    </Typography>
                                    {(!isAuthorized || appointment.Status === 'A' || appointment.Status === 'R') && (
                                      <Typography
                                        variant="button"
                                        display="block"
                                        gutterBottom
                                        sx={{
                                          backgroundColor: appointment.Status === 'A' ? 'green' : appointment.Status === 'R' ? 'red' : 'yellow',
                                          color: appointment.Status === 'A' || appointment.Status === 'R' ? 'white' : 'black',
                                          padding: '6px 16px',
                                          borderRadius: '4px',
                                          textAlign: 'center',
                                          display: 'inline-block',
                                          marginTop: '20px',
                                          marginbottom:'-20px'
                                        }}
                                      >
                                        {appointment.Status === 'A' ? 'Accepted' : appointment.Status === 'B' ? 'Booked' : appointment.Status === 'R' ? 'Rejected' : ''}
                                      </Typography>
                                    )}
                                    {isAuthorized && appointment.Status !== 'A' && appointment.Status !== 'R' && (
                                      <>
                                        <div style={{ display: 'flex', marginTop: "10px" }}>
                                          <Button
                                            variant="outlined"
                                            onClick={() => handleAccept(appointment.slot_id, appointment.slot_from, appointment.Meet_person)}
                                            sx={{ backgroundColor: "green", color: "white" ,}}
                                            size="small"
                                          >
                                            Accept
                                          </Button>&nbsp;
                                          <Button
                                            variant="outlined"
                                            onClick={() => handleReject(appointment.slot_id)}
                                            className='rejbtn'
                                            sx={{ backgroundColor: "red", marginLeft: "10%", color: "white" }}
                                            size="small"
                                          >
                                            Reject
                                          </Button>
                                        </div>
                                      </>
                                    )}
                                  </CardContent>
                                </Card>
                              </Grid>
                            ))
                          }
                        </Grid>
                      </Container>
                    ) : (
                      <Container sx={{ textAlign: 'center', padding: '20px' }}>
                        <CalendarTodayIcon sx={{ fontSize: 50, color: 'grey' }} />
                        <Typography variant="h6" sx={{ margin: '20px 0' }}>
                          You have no appointments on this date.
                        </Typography>
                      </Container>
                    )}
                  </div>
                </div>
              )}
            </div>
          </Container>
        )}
       <Dialog open={open} onClose={handleClose} fullWidth maxWidth="sm">
  {selectedAppointment ? (
    <>
      <DialogContent>
        <div style={{ padding: '20px' }}>
          <DialogTitle
            sx={{
              fontFamily: 'sans-serif',
              textAlign: 'center',
              marginBottom: '30px',
              boxShadow: 'inherit',
              backgroundColor: 'maroon',
              color: 'white',
            }}
          >
            Appointment Details
          </DialogTitle>

          <div style={{ marginTop: '20px' }}>
            <Typography color="maroon">Selected Slot Date</Typography>
            <TextField
              multiline
              required
              fullWidth
              value={formattedDate}
              readOnly
            />
          </div>
          <div>
            <div style={{ marginTop: '20px' }}>
              <Typography color="maroon">Appointed Person</Typography>
              <TextField
                multiline
                fullWidth
                required
                value={selectedAppointment.Employee_name}
                readOnly
              />
            </div>
            <h4 style={{ color: 'maroon', fontWeight: 'bold', marginTop: '15px' }}>Selected Slots:</h4>
            <RadioGroup>
              <Typography>{selectedAppointment.slot_from}--{selectedAppointment.slot_to}</Typography>
            </RadioGroup>
          </div>

          <div style={{ marginTop: '20px' }}>
            <Typography color="maroon">Discussion Topic</Typography>
            <TextField
              multiline
              required
              fullWidth
              value={selectedAppointment.Discussion_topic}
              readOnly
            />
          </div>
          <div style={{ marginTop: '20px' }}>
            <Typography color="maroon">Description</Typography>
            <TextField
              multiline
              required
              fullWidth
              value={selectedAppointment.Description}
              readOnly
            />
          </div>
          <Box sx={{ backgroundColor: 'maroon', color: 'white', marginTop: '20px', textAlign: 'center', padding: '5px', borderRadius: '10%' }}>
            People in Meeting
          </Box>
          <div style={{ marginTop: '20px' }}>
            <Typography color="maroon">Department</Typography>
            <TextField
              name="description"
              multiline
              fullWidth
              required
              value={selectedAppointment.Department}
              readOnly
            />
          </div>
          <div style={{ marginTop: '20px' }}>
            <Typography color="maroon">People Name</Typography>
            <TextField
              name="description"
              multiline
              fullWidth
              required
              value={selectedAppointment.Person_name}
              readOnly
            />
          </div>
        </div>
      </DialogContent>
      <DialogActions>
        {isAuthorized && selectedAppointment.Status !== 'A' && selectedAppointment.Status !== 'R' && (
          <div style={{ display: 'flex', marginTop: "10px" }}>
            <Button
              variant="outlined"
              onClick={() => handleAccept(selectedAppointment.slot_id, selectedAppointment.slot_from, selectedAppointment.Meet_person)}
              sx={{ backgroundColor: "green", color: "white" }}
              size="small"
            >
              Accept
            </Button>&nbsp;
           
          </div>
        )}
         <Button onClick={handleClose} sx={{ backgroundColor: 'maroon', color: 'white' }} variant="contained">
              Close
            </Button>
      </DialogActions>
    </>
  ) : (
    <DialogContent>
      <div style={{ padding: '20px', textAlign: 'center' }}>
        <Typography color="maroon">No appointment selected</Typography>
      </div>
    </DialogContent>
  )}
</Dialog>

      </div>
    </>
  );
}

export default EventViewer;

