import './App.css';
import Login from './Components/Login';

import { BrowserRouter, Routes, Route } from 'react-router-dom';
import AppointmentsReport from './Components/AppoinmentsReports';
import EventViewer from './Components/EventCal';
import { ThemeProvider, createTheme } from '@mui/material/styles';

const theme = createTheme({
  palette: {
    primary: {
      main: '#007bff',
    },
    secondary: {
      main: '#ff4081',
    },
  },
});

function App() {
  return (
    <div className="App">
      <BrowserRouter>
      <ThemeProvider theme={theme}>
        <Routes>
          <Route path='/' element={<Login />}/>
          <Route path='/dashboard' element={<EventViewer />} />
          <Route path='/reports' element={<AppointmentsReport/>} />
        </Routes>
        </ThemeProvider>
      </BrowserRouter>
    </div>
  );
}

export default App;