import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Box from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
import Avatar from '@mui/material/Avatar';
import MenuIcon from '@mui/icons-material/Menu';
import logo from './images/logo.jpeg';

function ResponsiveAppBar({ head }) {
  const [anchorElNav, setAnchorElNav] = useState(null);
  const [anchorElUser, setAnchorElUser] = useState(null);
  const [name, setName] = useState('');
  const [emp_id, setEmpId] = useState('');
  const datalocal = JSON.parse(localStorage.getItem("user"));

  useEffect(() => {
    if (datalocal !== null) {
      setName(datalocal?.emp_name || '');
      setEmpId(datalocal?.emp_id || '');
    }
  }, []);


  const authorizedIds = ['00055333', '00002712', '00055334','00001221'];
  const isAuthorized = authorizedIds.includes(emp_id);

  const settings = ['Logout'];

  const pages = ['Dashboard'];
  if (isAuthorized) {
    pages.push('Reports');
  }

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };

  const handleOpenUserMenu = (event) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  const navigate = useNavigate();
  const location = useLocation();

  const loggingout = () => {
    localStorage.removeItem('user');
    setName('');
    setEmpId('');
    console.log("Logging out...");
    navigate('/');
  };

  const handleMenuItemClick = (page) => {
    switch (page) {
      case 'Profile':
        navigate('/dashboard');
        break;
      case 'Reports':
        navigate('/reports');
        break;
      case 'Dashboard':
        navigate('/dashboard');
        break;
      case 'Logout':
        loggingout();
        break;
      default:
        break;
    }
    handleCloseUserMenu();
    handleCloseNavMenu();
  };

  return (
    <AppBar position="fixed" sx={{ backgroundColor: 'maroon', color: 'whitesmoke' }}>
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <Typography
            variant="h6"
            noWrap
            component="a"
            href="#app-bar-with-responsive-menu"
            sx={{
              mr: 2,
              display: { xs: 'none', md: 'flex' },
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: '.3rem',
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            <img src={logo} alt="logos" height="50px" />
          </Typography>
          <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              color="inherit"
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: 'block', md: 'none' },
                fontFamily: "sans-serif",
                fontWeight: "500"
              }}
            >
              {pages.map((page) => {
                const isActive = location.pathname === `/${page.toLowerCase()}`;
                return (
                  <MenuItem
                    key={page}
                    onClick={() => handleMenuItemClick(page)}
                    sx={{
                      textDecoration: isActive ? 'underline' : 'none',
                    }}
                  >
                    <Typography textAlign="center">{page}</Typography>
                  </MenuItem>
                );
              })}
            </Menu>
          </Box>

          <Typography
            variant="h5"
            noWrap
            component="a"
            href="#app-bar-with-responsive-menu"
            sx={{
              mr: 2,
              display: { xs: 'flex', md: 'none' },
              flexGrow: 1,
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: '.3rem',
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            <img src={logo} alt="logos" height="30px" />
          </Typography>
          <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
            {pages.map((page) => {
              const isActive = location.pathname === `/${page.toLowerCase()}`;
              return (
                <Button
                  key={page}
                  onClick={() => handleMenuItemClick(page)}
                  sx={{
                    my: 2,
                    color: 'white',
                    display: 'block',
                    textDecoration: isActive ? 'underline' : 'none',
                  }}
                >
                  {page}
                </Button>
              );
            })}
          </Box>

          <Box sx={{ flexGrow: 0 }}>
            <Tooltip title="Open settings">
              <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
              <Avatar alt={name}  />
                <Typography variant="h6" color="white">&nbsp;{name}</Typography>
              </IconButton>
            </Tooltip>
            <Menu
              sx={{ mt: '45px' }}
              id="menu-appbar"
              anchorEl={anchorElUser}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              open={Boolean(anchorElUser)}
              onClose={handleCloseUserMenu}
            >
              {settings.map((setting) => (
                <MenuItem key={setting} onClick={() => handleMenuItemClick(setting)}>
                  <Typography textAlign="center">{setting}</Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>





  );
}

export default ResponsiveAppBar;
