import React, { useState, useEffect } from 'react';
import {
  Button,
  Dialog,
  DialogTitle,
  DialogActions,
  TextField,
  Typography,
  Snackbar,
  Radio,
  RadioGroup,
  Alert,
  Autocomplete,
  MenuItem,
  Select,
  FormControlLabel
} from '@mui/material';
import axios from 'axios';
import ControlPointRoundedIcon from '@mui/icons-material/ControlPointRounded';
import './Styles/newapps.css';
import useAxios from '../axios';
import { useNavigate } from 'react-router-dom';

const NewAppointment = () => {
  const [open, setOpen] = useState(false);
  const [formData, setFormData] = useState({
    Date: '',
    topic: '',
    description: '',
    appointmentType: '',
    department: '',
    person: '',
    slot_time: []
  });
  const [selectedSlot, setSelectedSlot] = useState(null);
  const [click, setClick] = useState(false);
  const [departmentOptions, setDepartmentOptions] = useState([]);
  const [peopleOptions, setPeopleOptions] = useState([]);
  const [selectedDepartment, setSelectedDepartment] = useState(null);
  const [selectedPerson, setSelectedPerson] = useState(null);
  const [isAddPeopleClicked, setIsAddPeopleClicked] = useState(false);
  const [formDatas, setFormDatas] = useState({ department: '', person: '' });
  const [logs, setLogs] = useState('');
  const [appointmentType, setAppointmentType] = useState('');
  const [SelectedSlotIds, setSelectedSlotIds] = useState([]);
  const [slots, setSlots] = useState([]);
  const datalocal = JSON.parse(localStorage.getItem("user"));

  const [emp_id, setEmpId] = useState('');
  const [selectedAppointmentType, setSelectedAppointmentType] = useState('');

  
  const navigate=useNavigate();

  const dataAxios= useAxios();

  const axiosData=useAxios();
  useEffect(() => {
    if (datalocal !== null) {
      setEmpId(datalocal?.emp_id || '');
    }

    const fetchDepartments = async () => {
      try {
        const response = await axiosData.get('departments');
        if (response.data && Array.isArray(response.data)) {
          const departmentOptions = response.data.filter(department => department);
          setDepartmentOptions(departmentOptions);
        } else {
          console.error('Error fetching departments data: Invalid response format or null data');
        }
      } catch (error) {
        console.error('Error fetching departments data:', error);
      }
    };

    fetchDepartments();
  }, []);


  useEffect(() => {
    const fetchPeople = async () => {
      if (selectedDepartment && selectedDepartment.label) {
        const departmentName = selectedDepartment.label;
        try {
          const decodedDepartment = decodeURIComponent(departmentName);
          const response = await axiosData.get(`people/${decodedDepartment}`);
          if (Array.isArray(response.data)) {
            const peopleOptions = response.data.filter(person => person);
            setPeopleOptions(peopleOptions.map(person => person.C_EmployeeName));
          } else {
            console.error('Error fetching people data: Invalid response format or null data');
          }
        } catch (error) {
          console.error('Error fetching people data:', error);
        }
      }
    };

    fetchPeople();
  }, [selectedDepartment]);

  useEffect(() => {
    if (emp_id) {
      getLogDetail(emp_id);
    }
  }, [emp_id]);

  const getLogDetail = async (emp_id) => {
    try {
      const response = await axiosData.get(`user/${emp_id}`);
      const logdata = response.data;
      setLogs(logdata);
    } catch (error) {
      console.error('Error fetching appointments data:', error);
    }
  };

  const handleAddPeopleClick = () => {
    setIsAddPeopleClicked(true);
  };


  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData((prevData) => ({ ...prevData, [name]: value }));
  };

  const updateSlots = (type) => {
    // fetchAvailableSlots(type);
  };
  const handleSubmit = (event) => {
    event.preventDefault();
    if (formData.Date && selectedAppointmentType && selectedSlot && formData.topic && formData.description) {
      console.log('Form submitted:', {
        ...formData,
        selectedAppointmentType,
        selectedSlot,
        selectedDepartment,
        selectedPerson,
      });
      applyAppointment();
    } else {
      alert('Please fill in all required fields');
    }
  };

  const handleMethod = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setClick(false);
  };

  const handleClose = () => {
    setOpen(false);
  
  };



  const fetchAvailableSlots = async (appointmentType, selectedDate) => {
    try {
      console.log(`Fetching available slots for appointment type: ${appointmentType} on date: ${selectedDate}`);
      const response = await axiosData.get(`meetingslots/${appointmentType}`, {
        params: { date: selectedDate }
      });
      if (response.status === 200) {
        console.log('Raw slots data from server:', response.data);
        const validSlots = response.data.slots.filter(slot => slot.slot_from && slot.slot_to && slot.slot_date && slot.Status !== 'A'&& slot.Status !== 'B');
        console.log('Filtered valid slots:', validSlots);
        setSlots(validSlots);
      } else {
        console.warn('Unexpected response status:', response.status);
      }
    } catch (error) {
      console.error('Error fetching available slots:', error);
    }
  };
  const handleDateChange = (date) => {
    setFormData((prevData) => ({ ...prevData, Date: date }));
  };
  
  const handleTypeChange = async (event) => {
    const type = event.target.value;
    setSelectedAppointmentType(type);
    if (formData.Date) {
      await fetchAvailableSlots(type, formData.Date);
    }
  };
  
  const handleSlotChange = (event, slot) => {
    const { slot_id } = slot;
    setSelectedSlot(slot.slot_from);
    setSelectedSlotIds([slot_id]);
  }

  useEffect(() => {
    const appointmentTypes = appointmentType;
    updateSlots(appointmentTypes);
  }, []);

  useEffect(() => {
    console.log("Available slots:", slots);
  }, [slots]);

  const applyAppointment = async () => {
    const { Date, topic, description } = formData;

    try {
      const appointdata = await dataAxios.put(`meetingslots/${SelectedSlotIds}`, {
        slot_date: Date,
        Discussion_topic: topic,
        Description: description,
        Department: selectedDepartment ? selectedDepartment.label : '',
        Person_name: selectedPerson ? selectedPerson : '',
        Employee_id: logs.C_EmployeeCode,
        Employee_name: logs.C_EmployeeName,
        Employee_Department:logs.C_DepartmentName,
        user_type: 'Employee',
        Status: 'B'
      });

      console.log("appointdata.data", Date, topic, description, selectedAppointmentType, selectedPerson, logs.C_EmployeeName ,logs.C_DepartmentName);
      alert('Appointment successfully updated');
      handleClose();
      window.location.reload();
    } catch (error) {
      console.error('Error updating appointment:', error);
      alert('Failed to update appointment. Please try again.');
    }
  };

  return (
    <div className="leave_form">
      <Button startIcon={<ControlPointRoundedIcon />} onClick={() => setOpen(true)} sx={{ backgroundColor: 'maroon', color: 'white' }} variant="contained">
        New Appointment
      </Button>
      <Dialog open={open} onClose={handleClose} fullWidth maxWidth="sm">
        <div style={{ padding: '20px' }}>
          <DialogTitle
            sx={{
              fontFamily: 'sans-serif',
              textAlign: 'center',
              marginBottom: '30px',
              boxShadow: 'inherit',
              backgroundColor: 'maroon',
              color: 'white',
            }}
          >
            New Appointment Form
          </DialogTitle>
          <form onSubmit={handleSubmit}>
            <div className="date-container">
              <Typography color="maroon">Meeting Date</Typography>
              <input
                type="date"
                value={formData.Date}
                onChange={(e) => handleDateChange(e.target.value)}
                required
              />
            </div>
            <div style={{ marginTop: '20px' }}>
              <Typography color="maroon">Meet Person</Typography>
              <Select
                value={selectedAppointmentType}
                onChange={handleTypeChange}
                fullWidth
                required
              >
                <MenuItem value="SANJJAY">SANJJAY</MenuItem>
                <MenuItem value="SMARAN">SMARAN</MenuItem>
                <MenuItem value="SAUMYHA">SAUMYHA</MenuItem>
              </Select>
              <h4 style={{ color: 'maroon', fontWeight: 'bold', marginTop: '15px' }}>Available Slots:</h4>
              {selectedAppointmentType ? (
                slots.length > 0 ? (
                  <div>
                    <Typography color="maroon" style={{ marginTop: '20px' }}>Select Available Slot</Typography>
                    <RadioGroup
                      value={selectedSlot}
                      onChange={(event) => handleSlotChange(event, slots.find(slot => slot.slot_from === event.target.value))}
                    >
                      {slots.map((slot, index) => (
                        <FormControlLabel
                          key={index}
                          control={
                            <Radio
                              value={slot.slot_from}
                              disabled={slot.appoinment_status === 'accepted'}
                              style={{ color: 'maroon' }}
                            />
                          }
                          label={`${slot.slot_from} - ${slot.slot_to}`}
                        />
                      ))}
                    </RadioGroup>
                  </div>
                ) : (
                  <p>No Available Slots on Selected Date</p>
                )
              ) : (
                <p>Select the Meet Person</p>
              )}
            </div>
            <div style={{ marginTop: '20px' }}>
              <Typography color="maroon">Discussion Topic</Typography>
              <TextField
                name="topic"
                label="Reason"
                multiline
                required
                fullWidth
                value={formData.topic}
                onChange={handleChange}
              />
            </div>
            <div style={{ marginTop: '20px' }}>
              <Typography color="maroon">Description</Typography>
              <TextField
                name="description"
                label="Description"
                multiline
                rows={3}
                fullWidth
                required
                value={formData.description}
                onChange={handleChange}
              />
            </div>
            <div style={{ marginTop: '20px' }}>
              <Typography color="maroon">Add People</Typography>
              <Button onClick={handleAddPeopleClick} sx={{ backgroundColor: 'maroon', color: 'white' }} variant="outlined">
                Add People
              </Button>
              {isAddPeopleClicked && (
                <div>
                  <div style={{ marginTop: "20px" }}>
                    <Autocomplete
                      value={selectedDepartment}
                      onChange={(event, newValue) => {
                        setSelectedDepartment(newValue);
                        setFormDatas((prevData) => ({ ...prevData, department: newValue }));
                      }}
                      inputValue={selectedDepartment ? selectedDepartment.label : ''}
                      onInputChange={(event, newInputValue) => {
                        setSelectedDepartment({ label: newInputValue });
                      }}
                      options={departmentOptions.filter(option => option && option.label)}
                      getOptionLabel={(option) => option && option.label ? option.label : ''}
                      renderInput={(params) => (
                        <TextField {...params} label="Departments" fullWidth />
                      )}
                    />
                  </div>
                  <div style={{ marginTop: '20px' }}>
                    <Autocomplete
                      value={selectedPerson}
                      onChange={(event, newValue) => {
                        setSelectedPerson(newValue);
                      }}
                      inputValue={selectedPerson ? selectedPerson : ''}
                      onInputChange={(event, newInputValue) => {
                        setSelectedPerson(newInputValue);
                      }}
                      options={peopleOptions}
                      getOptionLabel={(option) => option || ''}
                      renderInput={(params) => <TextField {...params} label="People" fullWidth />}
                    />
                  </div>
                </div>
              )}
            </div>
            <DialogActions>
              <Button onClick={handleClose} sx={{ backgroundColor: 'maroon', color: 'white' }} variant="contained">
                Cancel
              </Button>
              <Button type="submit" sx={{ backgroundColor: 'maroon', color: 'white' }} variant="contained">
                Book Appointment
              </Button>
            </DialogActions>
          </form>
        </div>
      </Dialog>
      <Snackbar open={click} autoHideDuration={6000} onClose={handleMethod}>
        <Alert onClose={handleMethod} severity="success" sx={{ width: '100%' }}>
          Appointment Successfully Booked!
        </Alert>
      </Snackbar>
    </div>
  );
};

export default NewAppointment;
