import React, { useState, useEffect } from 'react';
import {
  Container,
  TextField,
  Button,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Box
} from '@mui/material';
import { styled } from '@mui/system';
import axios from 'axios';
import ResponsiveAppBar from './Navbar';
import { BounceLoader } from 'react-spinners';
import useAxios from '../axios';
import { useMediaQuery, useTheme } from '@mui/material';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  fontWeight: 'bold',
  backgroundColor: theme.palette.grey[200] || '#f5f5f5',
  border: `1px solid ${theme.palette.divider || '#ddd'}`,
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(odd)': {
    backgroundColor: theme.palette.action.hover || '#fafafa',
  },
  border: `1px solid ${theme.palette.divider || '#ddd'}`,
}));

const AppointmentsReport = () => {
  const [fromDate, setFromDate] = useState('');
  const [toDate, setToDate] = useState('');
  const [loading, setLoading] = useState(false);
  const [appointments, setAppointments] = useState([]);
  const [searchInitiated, setSearchInitiated] = useState(false);
  const [empId, setEmpId] = useState('');
  const [loadings, setLoadings] = useState(true);

  const datalocal = JSON.parse(localStorage.getItem('user'));
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  useEffect(() => {
    if (datalocal !== null) {
      setEmpId(datalocal?.emp_id || '');
    }
    setTimeout(() => {
      setLoadings(false);
    }, 2000);
  }, []);

  const axiosData = useAxios();

  const handleSearch = async () => {
    setSearchInitiated(true);
    setLoading(true);
    try {
        let response;
        if (empId === '00001221') {
            response = await axiosData.get(`md-appoinments/${empId}?fromDate=${fromDate}&toDate=${toDate}`);
        } else {
            response = await axiosData.get(`dates/md-appointmentsreports/${empId}?fromDate=${fromDate}&toDate=${toDate}`);
        }
  
        const filteredData = response.data.filter(
            (appointment) => !['S'].includes(appointment.Status)
        );
        setAppointments(filteredData);
    } catch (error) {
        console.error('Error fetching appointments data:', error);
    } finally {
        setLoading(false);
    }
};

  return (
    <>
      <ResponsiveAppBar />
      <div>
        {loadings ? (
          <div
            style={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              height: '100vh',
            }}
          >
            <BounceLoader color={'maroon'} loading={loadings} size={60} />
          </div>
        ) : (
          <Container maxWidth="lg"  sx={{ minWidth: isMobile ? '90%' : '90%'  ,marginTop: isMobile ?'18%' : '8%' }}>
            <Box my={4}>
              <Typography
                style={{
                  fontSize: '1.5rem',
                  fontWeight: 600,
                  marginTop: '5%',
                  marginBottom: '10px',
                  color: 'maroon',
                }}
                variant="h4"
                gutterBottom
              >
                Appointments Report
              </Typography>
              <Box mb={2} sx={{ marginTop: '3.5%' }}>
                <TextField
                  label="From Date"
                  type="date"
                  value={fromDate}
                  onChange={(e) => setFromDate(e.target.value)}
                  InputLabelProps={{ shrink: true }}
                  sx={{ mr: 2 }}
                />
                <TextField
                  label="To Date"
                  type="date"
                  value={toDate}
                  onChange={(e) => setToDate(e.target.value)}
                  InputLabelProps={{ shrink: true }}
                />
              </Box>
              <Button
                variant="contained"
                style={{
                  fontSize: '1.2rem',
                  fontWeight: 500,
                  marginBottom: '10px',
                  backgroundColor: 'maroon',
                  color: 'white',
                }}
                onClick={handleSearch}
              >
                Search
              </Button>

              {!searchInitiated && (
                <Typography
                  variant="h6"
                  color="textSecondary"
                  align="center"
                  mt={4}
                >
                  Please select the dates and click search to view appointments.
                </Typography>
              )}
              {searchInitiated && (
                <>
                  <Box mt={4}>
                    {appointments.length > 0 ? (
                      <>
                        <Typography
                          sx={{
                            fontFamily: 'sans-serif',
                            fontWeight: 'bold',
                            color: 'maroon',
                            marginBottom: '3%',
                          }}
                          variant="h6"
                          gutterBottom
                        >
                          Appointments
                        </Typography>
                        <TableContainer component={Paper} >
                          <Table >
                            <TableHead>
                              <TableRow>
                                <StyledTableCell>Date</StyledTableCell>
                                <StyledTableCell>Employee Name</StyledTableCell>
                                <StyledTableCell>Employee Department</StyledTableCell>
                                <StyledTableCell>Discussion Topic</StyledTableCell>
                                <StyledTableCell>Description</StyledTableCell>
                                <StyledTableCell>Slot from</StyledTableCell>
                                <StyledTableCell>Slot to</StyledTableCell>
                                <StyledTableCell>Meet Person</StyledTableCell>
                                <StyledTableCell>Added People Name</StyledTableCell>
                                <StyledTableCell>Added Department from </StyledTableCell>
                                <StyledTableCell>Status</StyledTableCell>
                              </TableRow>
                            </TableHead>
                            <TableBody>
                              {appointments.map((appointment) => {
                                let formattedDate = '';
                                if (appointment.slot_date) {
                                  const date = new Date(appointment.slot_date);
                                  if (!isNaN(date)) {
                                    const day = String(date.getDate()).padStart(2, '0');
                                    const month = String(date.getMonth() + 1).padStart(2, '0');
                                    const year = date.getFullYear();
                                    formattedDate = `${day}/${month}/${year}`;
                                  }
                                }

                                let statusAbbreviation = '';
                                switch (appointment.Status) {
                                  case 'A':
                                    statusAbbreviation = 'Accepted';
                                    break;
                                  case 'R':
                                    statusAbbreviation = 'Rejected';
                                    break;
                                  case 'B':
                                    statusAbbreviation = 'Booked';
                                    break;
                                  default:
                                    statusAbbreviation = appointment.Status;
                                }

                                return (
                                  <StyledTableRow key={appointment.id}>
        <TableCell sx={{ width: '150px' }}>{formattedDate}</TableCell>
        <TableCell sx={{ width: '120px' }}>{appointment.Employee_name}</TableCell>
        <TableCell>{appointment.Employee_Department}</TableCell>
        <TableCell>{appointment.Discussion_topic}</TableCell>
        <TableCell>{appointment.Description}</TableCell>
        <TableCell sx={{ width: '120px' }}>{appointment.slot_from}</TableCell>
        <TableCell sx={{ width: '120px' }}>{appointment.slot_to}</TableCell>
        <TableCell>{appointment.Meet_person}</TableCell>
        <TableCell>{appointment.Person_name}</TableCell>
        <TableCell>{appointment.Department}</TableCell>
        <TableCell>{statusAbbreviation}</TableCell>
      </StyledTableRow>
                                );
                              })}
                            </TableBody>
                          </Table>
                        </TableContainer>
                      </>
                    ) : (
                      <Typography variant="h6" color="textSecondary" align="center">
                        There are no appointments for the selected date range.
                      </Typography>
                    )}
                  </Box>
                </>
              )}
            </Box>
          </Container>
        )}
      </div>
    </>
  );
};

export default AppointmentsReport;
