import React, { useState, useEffect } from 'react';
import {
  Typography,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Select,
  MenuItem,
  FormGroup,
  FormControlLabel,
  Checkbox,
} from '@mui/material';
import ControlPointRoundedIcon from '@mui/icons-material/ControlPointRounded';
import useAxios from '../axios';

const SlotTime = () => {
  const [open, setOpen] = useState(false);
  const [appointmentType, setAppointmentType] = useState('');
  const [slots, setSlots] = useState([]);
  const [selectedSlots, setSelectedSlots] = useState([]);
  const [successDialogOpen, setSuccessDialogOpen] = useState(false);
  const [meetPersonID, setMeetPersonID] = useState('');
  const [selectedDate, setSelectedDate] = useState('');
  const [selectAll, setSelectAll] = useState(false);

  useEffect(() => {
    if (appointmentType && selectedDate) {
      fetchDefaultSlots(appointmentType, selectedDate);
    }
  }, [appointmentType, selectedDate]);

  const dataAxios = useAxios();
  const axiosData = useAxios();

  const handleTypeChange = (e) => {
    const value = e.target.value;
    let meetPersonID;
  
    switch (value) {
      case 'SANJJAY':
        meetPersonID = '00055333';
        break;
      case 'SMARAN':
        meetPersonID = '00002712';
        break;
      case 'Saumyah':
        meetPersonID = '00055334';
        break;
      default:
        meetPersonID = '00';
        break;
    }
  
    setAppointmentType(value);
    setMeetPersonID(meetPersonID);
  };

  const fetchDefaultSlots = async (type, date) => {
    try {
      const response = await axiosData.get(`slot_default/${type}?date=${date}`);
      if (response.status === 200) {
        const filteredSlots = response.data.slots;
        setSlots(filteredSlots);
      }
    } catch (error) {
      console.error('Error fetching default slots:', error);
    }
  };

  const handleSlotChange = (event) => {
    const { value, checked } = event.target;
    setSelectedSlots((prevSlots) => {
      if (checked) {
        return [...prevSlots, value];
      } else {
        return prevSlots.filter((slot) => slot !== value);
      }
    });
    setSelectAll(slots.length === selectedSlots.length + 1 && checked);
  };

  const handleSelectAllChange = (event) => {
    const isChecked = event.target.checked;
    setSelectAll(isChecked);
    if (isChecked) {
      setSelectedSlots(slots.map(slot => `${slot.slot_from} - ${slot.slot_to}`));
    } else {
      setSelectedSlots([]);
    }
  };

  const handleDateChange = (event) => {
    setSelectedDate(event.target.value);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    const postData = selectedSlots.map((slot) => {
      const [slot_from, slot_to] = slot.split(' - ');
      return {
        slot_from,
        slot_to,
        slot_date: selectedDate,
        to_meet: appointmentType,
        meetPersonID,
        appointment_status: 'S',
      };
    });
  
    console.log('Post Data:', postData); // Log the postData array to verify its structure
  
    try {
      const response = await dataAxios.post(`meetingslots/${appointmentType}`, postData);
  
      if (response.status === 201) {
        setSuccessDialogOpen(true);
        setOpen(false);
      } else {
        console.error('Error posting data');
      }
    } catch (error) {
      console.error('Error:', error);
    }
  };

  const handleCloseSuccessDialog = () => {
    setSuccessDialogOpen(false);
  };

  return (
    <>
      <div className="leave_form">
        <Button
          startIcon={<ControlPointRoundedIcon />}
          onClick={() => setOpen(true)}
          sx={{ backgroundColor: 'maroon', color: 'white' }}
          variant="contained"
        >
          Time Slot Picker
        </Button>
        <Dialog open={open} onClose={() => setOpen(false)} fullWidth maxWidth="sm">
          <div style={{ padding: '20px' }}>
            <DialogTitle
              sx={{
                fontFamily: 'sans-serif',
                textAlign: 'center',
                marginBottom: '30px',
                boxShadow: 'inherit',
                backgroundColor: 'maroon',
                color: 'white',
              }}
            >
              New Time Slot
            </DialogTitle>
            <form onSubmit={handleSubmit}>
              <div style={{ marginTop: '20px' }}>
                <Typography color="maroon">Appointment For</Typography>
                <Select value={appointmentType} onChange={handleTypeChange} fullWidth>
                  <MenuItem value="SANJJAY">SANJJAY</MenuItem>
                  <MenuItem value="SMARAN">SMARAN</MenuItem>
                  <MenuItem value="Saumyah">Saumyah</MenuItem>
                </Select>
                <Typography color="maroon"  style={{ marginTop: '20px' }}>
                  Select Date
                </Typography>
                <div className="date-container">
                  <input type="date" value={selectedDate} onChange={handleDateChange} required />
                </div>
                {slots.length > 0 && (
                  <div>
                    <Typography color="maroon" style={{ marginTop: '20px' }}>
                      Select Available Slots
                    </Typography>
                    <FormGroup>
                      <FormControlLabel
                        control={<Checkbox checked={selectAll} onChange={handleSelectAllChange} />}
                        label="Select All"
                      />
                      {slots.map((slot) => (
                        <FormControlLabel
                          key={slot.slot_from + slot.slot_to}
                          control={
                            <Checkbox
                              value={`${slot.slot_from} - ${slot.slot_to}`}
                              checked={selectedSlots.includes(`${slot.slot_from} - ${slot.slot_to}`)}
                              onChange={handleSlotChange}
                            />
                          }
                          label={`${slot.slot_from} - ${slot.slot_to}`}
                        />
                      ))}
                    </FormGroup>
                  </div>
                )}
              </div>
              <Button type="submit" sx={{ backgroundColor: 'maroon', color: 'white', marginTop: '20px' }} variant="contained">
                Update the Slots
              </Button>
            </form>
          </div>
        </Dialog>
        <Dialog open={successDialogOpen} onClose={handleCloseSuccessDialog}>
          <DialogTitle>Success</DialogTitle>
          <DialogContent>
            <Typography>Your slots have been successfully updated!</Typography>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleCloseSuccessDialog} color="primary">
              Close
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    </>
  );
};

export default SlotTime;
