import React, { useState ,useEffect} from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import logo from './images/logo.jpeg';
import { useNavigate } from 'react-router-dom';
import './Styles/Login.css';
import { BounceLoader } from 'react-spinners';
import Loading from './Loading';
import useAxios from '../axios';

const MyForm = () => {
    const [emp_id, setEmpId] = useState('');
    const [password, setPassword] = useState('');
    const navigate = useNavigate();
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
      // Simulate a delay for loading (e.g., fetch data or load resources)
      const timer = setTimeout(() => {
        setIsLoading(false);
      }, 3000); // Adjust the delay as necessary
  
      return () => clearTimeout(timer);
    }, []);
  
        const axiosData = useAxios(); 

        const handleSubmit = async (event) => {
            event.preventDefault();
        
            try {
              console.log('Attempting to fetch data...');
              const response = await axiosData.get(`user/${emp_id}`);
              console.log('Data fetched:', response.data);
        
              if (response.data !== undefined) {
                if (response.data.C_EmployeeCode === password) {
                  console.log('Password matched');
        
                  localStorage.setItem('user', JSON.stringify({
                    emp_name: response.data.C_EmployeeName,
                    emp_id: response.data.C_EmployeeCode,
                  }));
        
                  navigate('/dashboard', { state: { Emp_name: response.data.C_EmployeeName, label: 'Dashboard' } });
                } else {
                  console.log('Password wrong');
                  alert('You have entered an incorrect credential');
                }
              }
            } catch (err) {
              console.log('Error occurred:', err);
              if (!err.response) {
                // Network error or server down
                alert('Connection lost. Please try again later.');
              } else if (err.response.status === 401) {
                // Unauthorized error (incorrect credentials)
                alert('Incorrect password.');
              } else {
                // Other errors
                alert('An error occurred. Please try again.');
              }
            }
          };
        
        

    return (

<>
      {isLoading ? (
        <Loading />
      ) : (
         <div>
       
        <Box
            sx={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                height: '100vh',
            }}
        >
            <div className="boxes">
                <img src={logo} alt="logo" />
                <div className="formfield">
                    <form onSubmit={handleSubmit}>
                        <label>
                            <Typography variant="h6" className="fields" fontWeight={600}>
                                Employee ID
                                <TextField  label="ID" required name='emp_id' sx={{ m: 1, width: '100%' }} onChange={e => setEmpId(e.target.value)} />
                            </Typography>
                        </label>
                        <label>
                            <Typography variant="h6" className="fields" fontWeight={600}>
                                Password
                                <TextField  label="Password" required name='emp_password' type="password" sx={{ m: 1, width: '100%' }} onChange={e => setPassword(e.target.value)} />
                            </Typography>
                        </label>
                        <Button variant="contained" type="submit" sx={{ m: 1, backgroundColor: 'maroon', color: 'whitesmoke' }}>
                            Login
                        </Button>
                    </form>
                </div>
            </div>
        </Box>
        
        </div>
         )}
    </>
    );
    
};

export default MyForm;
